<template>
	<view class="content">
		<image class="logo" :src="headerUrl"></image>
		<view class="text-area">
			<text class="title">{{nickName}}</text>
		</view>
    <view class="text-area button-style" @click=login>
      登录
    </view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				nickName: 'Hello',
        headerUrl:'../../static/logo.png'
			}
		},
		onLoad() {

		},
		methods: {
    login(){
      let _this = this
      uni.showModal({
        mask:true,
        title:'温馨提示',
        content:'授权微信登录后才能正常使用小程序功能',
        success(res) {
          if(res.confirm){
            uni.getUserProfile({
              desc:'获取你的昵称,头像',
              success:res=>{
                console.log('@@@@',res)
                if(res.errMsg=='getUserProfile:ok'&&res.userInfo!==undefined){
                  let userInfo = {
                    avatarUrl:res.userInfo.avatarUrl,
                    nickName:res.userInfo.nickName
                  }
                  //对页面中的变量进行赋值
                  _this.nickName = res.userInfo.nickName
                  _this.headerUrl = res.userInfo.avatarUrl
                  //调用接口请求openid
                  _this.getUserOpenId(userInfo)
                }else{
                  uni.showToast({
                    icon:'none',
                    title:'获取失败，请重试'
                  })
                }
              }
            })
          }
        }
      })
    },
    getUserOpenId(userInfo){
      let _this = this
      uni.login({
        provider: 'weixin', //使用微信登录
          success: (res)=>{
            console.log('###$$$',res)
          }
      })
    }
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
  .button-style{
    margin-top: 30rpx;
    background-color: red;
  }
</style>
